<template>
  <div>
    <div v-for="post in posts" :key="post.id" id="post">
      <div v-if="!post.edit">
        <p>Title: {{ post.title }}</p>
        <p>Body: {{ post.body }}</p>
        <button @click="deletePost(post)">Delete</button>
        <button @click="updatePost(post)">Update</button>
      </div>
      <div v-else>
        Title: <input type="text" v-model="post.title">
        Body: <input type="text" v-model="post.body">
        
        <button @click="save(post)">Save</button>
      </div>
      <br>
    </div>
    <form @submit.prevent="addPost()">
      <label>Title</label>
        <input
          id="title"
          name="title"
          type="text"
          class="form-control here"
          v-model="post.title">
      <label>Body</label>
        <input
          id="title"
          name="title"
          type="text"
          class="form-control here"
          v-model="post.body">
      <button name="submit" type="submit">Add</button>
    </form>
  </div>

</template>

<script>
import { db, ref } from '../main'

export default {
  name: 'HelloWorld',
  data(){
    return {
      post: {
        body: '',
        title: '',
        edit: false
      },
      posts: [],
      
    }
    
  },
  
  created(){
    this.getPosts()
  },
  methods: {
     getPosts(){
      db.ref('blog/posts').once('value')
      .then((data) => {
       // console.log(data.val())
        let object = data.val()
        for (let key in object){
          this.posts.push({
            id: key,
            title: object[key].title,
            body: object[key].body,
            edit: false
          })
        }
      })
     
     },
    addPost(){
      db.ref('blog/posts').push(this.post)
     .then((data) => {
       this.post.id = data.key
       this.posts.push(this.post)
       this.post = {}
       //console.log(this.post)
     })
      
    },
    deletePost(post){
     db.ref('blog/posts/' + post.id).remove()
     let index = this.posts.indexOf(post)
     this.posts.splice(index, 1)
 
    },
    updatePost(post){
      db.ref('blog/posts/' + post.id).update({edit: true})
      post.edit = true

    },

    save(post){
      db.ref('blog/posts/' + post.id).update({
        edit: false, 
        title: post.title,
        body: post.body
      })
      post.edit = false
    }
  },
}
</script>

